	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import discusszhiwuyuanxinxi from '@/views/discusszhiwuyuanxinxi/list'
	import zhiwuyuanxinxi from '@/views/zhiwuyuanxinxi/list'
	import yonghu from '@/views/yonghu/list'
	import chat from '@/views/chat/list'
	import orders from '@/views/orders/list'
	import storeup from '@/views/storeup/list'
	import config from '@/views/config/list'
	import users from '@/views/users/list'
	import cart from '@/views/cart/list'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/news',
			name: '',
			component: news
		}
		,{
			path: '/discusszhiwuyuanxinxi',
			name: '',
			component: discusszhiwuyuanxinxi
		}
		,{
			path: '/zhiwuyuanxinxi',
			name: '',
			component: zhiwuyuanxinxi
		}
		,{
			path: '/yonghu',
			name: '',
			component: yonghu
		}
		,{
			path: '/chat',
			name: '',
			component: chat
		}
		,{
			path: '/orders',
			name: '',
			component: orders
		}
		,{
			path: '/storeup',
			name: '',
			component: storeup
		}
		,{
			path: '/config',
			name: '',
			component: config
		}
		,{
			path: '/users',
			name: '',
			component: users
		}
		,{
			path: '/cart',
			name: '',
			component: cart
		}
		]
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
