<template>
	<div>
		<el-dialog v-model="formVisible" :title="formTitle" width="80%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" label-width="$template2.back.add.form.base.labelWidth" :rules="rules">
				<el-row>
					<el-col :span="24">
						<el-form-item label="" prop="zhiwuyuanmingcheng">
							<el-input class="list_inp" v-model="form.zhiwuyuanmingcheng" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.zhiwuyuanmingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="zhiwuyuandizhi">
							<el-input class="list_inp" v-model="form.zhiwuyuandizhi" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.zhiwuyuandizhi?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="zhiwuyuantupian">
							<uploads
								:disabled="!isAdd||disabledForm.zhiwuyuantupian?true:false"
								action="file/upload" 
								tip="" 
								:limit="3" 
								style="width: 100%;text-align: left;"
								:fileUrls="form.zhiwuyuantupian?form.zhiwuyuantupian:''" 
								@change="zhiwuyuantupianUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="kaiyuanshijian">
							<el-input class="list_inp" v-model="form.kaiyuanshijian" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.kaiyuanshijian?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="fuwudianhua">
							<el-input class="list_inp" v-model="form.fuwudianhua" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.fuwudianhua?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="reversetime">
							<el-date-picker
								class="list_date"
								v-model="form.reversetime"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:readonly="!isAdd||disabledForm.reversetime?true:false"
								placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="price">
							<el-input class="list_inp" v-model.number="form.price" placeholder=""
								 type="number" 								:readonly="!isAdd||disabledForm.price?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="groupprice">
							<el-input class="list_inp" v-model.number="form.groupprice" placeholder=""
								 type="number" 								:readonly="!isAdd||disabledForm.groupprice?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="grouppeople">
							<el-input class="list_inp" v-model.number="form.grouppeople" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.grouppeople?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="curpeople">
							<el-input class="list_inp" v-model.number="form.curpeople" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.curpeople?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="zhiwuyuanjianjie">
							<el-input v-model="form.zhiwuyuanjianjie" placeholder="" type="textarea"
							:readonly="!isAdd||disabledForm.zhiwuyuanjianjie?true:false"
							/>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="goupiaoxuzhi">
							<el-input v-model="form.goupiaoxuzhi" placeholder="" type="textarea"
							:readonly="!isAdd||disabledForm.goupiaoxuzhi?true:false"
							/>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="zhiwuyuanxiangqing">
							<editor :value="form.zhiwuyuanxiangqing" placeholder="" :readonly="!isAdd||disabledForm.zhiwuyuanxiangqing?true:false"
								class="list_editor" @change="(e)=>editorChange(e,'zhiwuyuanxiangqing')"></editor>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="formModel_cancel" @click="closeClick"></el-button>
					<el-button class="formModel_confirm" type="primary" @click="save"
						>
						
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//
	const tableName = 'zhiwuyuanxinxi'
	const formName = ''
	//
	//form
	const form = ref({})
	const disabledForm = ref({
		zhiwuyuanmingcheng : false,
		zhiwuyuandizhi : false,
		zhiwuyuanjianjie : false,
		zhiwuyuantupian : false,
		kaiyuanshijian : false,
		fuwudianhua : false,
		goupiaoxuzhi : false,
		zhiwuyuanxiangqing : false,
		reversetime : false,
		price : false,
		groupprice : false,
		grouppeople : false,
		curpeople : false,
		clicktime : false,
		storeupnum : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
	//
	//
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("URL"));
		} else {
			callback();
		}
	}
	const rules = ref({
		zhiwuyuanmingcheng: [
		],
		zhiwuyuandizhi: [
		],
		zhiwuyuanjianjie: [
		],
		zhiwuyuantupian: [
		],
		kaiyuanshijian: [
		],
		fuwudianhua: [
			{ validator: validateMobile, trigger: 'blur' },
		],
		goupiaoxuzhi: [
		],
		zhiwuyuanxiangqing: [
		],
		reversetime: [
		],
		price: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		groupprice: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		grouppeople: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		curpeople: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		clicktime: [
		],
		storeupnum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
	})
	//
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//
	const zhiwuyuantupianUploadSuccess=(e)=>{
		form.value.zhiwuyuantupian = e
	}
	//methods

	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//
	const resetForm = () => {
		form.value = {
			zhiwuyuanmingcheng: '',
			zhiwuyuandizhi: '',
			zhiwuyuanjianjie: '',
			zhiwuyuantupian: '',
			kaiyuanshijian: '',
			fuwudianhua: '',
			goupiaoxuzhi: '',
			zhiwuyuanxiangqing: '',
			reversetime: '',
			price: '0',
			groupprice: '0',
			grouppeople: '0',
			curpeople: '0',
			clicktime: '',
			storeupnum: '0',
		}
	}
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			res.data.data.zhiwuyuanxiangqing = res.data.data.zhiwuyuanxiangqing?(res.data.data.zhiwuyuanxiangqing.replace(reg,'../../../cl62716125/file')):'';
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='zhiwuyuanmingcheng'){
					form.value.zhiwuyuanmingcheng = row[x];
					disabledForm.value.zhiwuyuanmingcheng = true;
					continue;
				}
				if(x=='zhiwuyuandizhi'){
					form.value.zhiwuyuandizhi = row[x];
					disabledForm.value.zhiwuyuandizhi = true;
					continue;
				}
				if(x=='zhiwuyuanjianjie'){
					form.value.zhiwuyuanjianjie = row[x];
					disabledForm.value.zhiwuyuanjianjie = true;
					continue;
				}
				if(x=='zhiwuyuantupian'){
					form.value.zhiwuyuantupian = row[x];
					disabledForm.value.zhiwuyuantupian = true;
					continue;
				}
				if(x=='kaiyuanshijian'){
					form.value.kaiyuanshijian = row[x];
					disabledForm.value.kaiyuanshijian = true;
					continue;
				}
				if(x=='fuwudianhua'){
					form.value.fuwudianhua = row[x];
					disabledForm.value.fuwudianhua = true;
					continue;
				}
				if(x=='goupiaoxuzhi'){
					form.value.goupiaoxuzhi = row[x];
					disabledForm.value.goupiaoxuzhi = true;
					continue;
				}
				if(x=='zhiwuyuanxiangqing'){
					form.value.zhiwuyuanxiangqing = row[x];
					disabledForm.value.zhiwuyuanxiangqing = true;
					continue;
				}
				if(x=='reversetime'){
					form.value.reversetime = row[x];
					disabledForm.value.reversetime = true;
					continue;
				}
				if(x=='price'){
					form.value.price = row[x];
					disabledForm.value.price = true;
					continue;
				}
				if(x=='groupprice'){
					form.value.groupprice = row[x];
					disabledForm.value.groupprice = true;
					continue;
				}
				if(x=='grouppeople'){
					form.value.grouppeople = row[x];
					disabledForm.value.grouppeople = true;
					continue;
				}
				if(x=='curpeople'){
					form.value.curpeople = row[x];
					disabledForm.value.curpeople = true;
					continue;
				}
				if(x=='clicktime'){
					form.value.clicktime = row[x];
					disabledForm.value.clicktime = true;
					continue;
				}
				if(x=='storeupnum'){
					form.value.storeupnum = row[x];
					disabledForm.value.storeupnum = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.price='0'
			form.value.groupprice='0'
			form.value.grouppeople='0'
			form.value.curpeople='0'
			form.value.storeupnum='0'
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
		})
	}
	//
	//
	defineExpose({
		init
	})
	//
	const closeClick = () => {
		formVisible.value = false
	}
	//
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//
	const save=()=>{
		if(form.value.zhiwuyuantupian!=null) {
			form.value.zhiwuyuantupian = form.value.zhiwuyuantupian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(``,'success',()=>{
									formVisible.value = false
									emit('formModelChange')
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(``,'success',()=>{
							formVisible.value = false
							emit('formModelChange')
						})
					})
				}
			}
		})
	}
	//
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
</script>
<style lang="scss" scoped>
	// 
	.formModel_form{
		border-radius: 6px;
		padding: 30px;
		// form item
		:deep(.el-form-item) {
			margin: 0 0 20px 0;
			display: flex;
			//label
			.el-form-item__label {
			 color: #9E9E9E;
			 display: block;
			 width: 90px;
			 text-align: center;
			}
			// 
			.el-form-item__content {
				display: flex;
				width: calc(100% - 90px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 
				.list_inp {
					border: 2px solid #5FB7FF;
					border-radius: 20px;
					padding: 0 10px;
					background: #fff;
					width: 100%;
					line-height: 36px;
					box-sizing: border-box;
					height: 36px;
					//
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				//
				.list_date {
					border: 2px solid #5FB7FF;
					border-radius: 20px;
					background: #fff;
					width: 100%;
					line-height: 36px;
					box-sizing: border-box;
					//
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
					}
				}
				// 
				.list_editor {
					background-color: #fff;
					border-radius: 0;
					padding: 0;
					margin: 0;
					width: 100%;
					border-color: #5FB7FF;
					border-width: 2px;
					border-style: solid;
					height: auto;
				}
				// 
				.el-textarea__inner {
					border: 2px solid #5FB7FF;
					border-radius: 20px;
					padding: 12px;
					outline: none;
					color: #333;
					background: #fff;
					width: 400px;
					font-size: 14px;
					min-height: 120px;
				}
				//
				.el-upload-list  {
					//
					.el-upload__tip {
						margin: 7px 0 0;
						color: #999;
						display: none;
						font-size: 12px;
						justify-content: flex-start;
						align-items: center;
					}
					//
					.el-upload--picture-card {
						border: 2px solid #5FB7FF;
						cursor: pointer;
						background-color: #fff;
						border-radius: 20px;
						width: 90px;
						line-height: 100px;
						text-align: center;
						height: 90px;
						//
						.el-icon{
							color: #9E9E9E;
							font-size: 32px;
						}
					}
					.el-upload-list__item {
						border: 2px solid #5FB7FF;
						cursor: pointer;
						background-color: #fff;
						border-radius: 20px;
						width: 90px;
						line-height: 100px;
						text-align: center;
						height: 90px;
					}
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: center;
		.formModel_cancel {
			border: 0;
			cursor: pointer;
			border-radius: 4px;
			padding: 0 24px;
			margin: 0 10px 0 0;
			outline: none;
			color: #B8B8B8;
			background: #CDE9FF;
			width: auto;
			font-size: 14px;
			height: 40px;
		}
		.formModel_cancel:hover {
			color: #333;
			background: rgba(205, 233, 255, 0.5);
			font-size: 16px;
		}
		
		.formModel_confirm {
			border: 0;
			cursor: pointer;
			border-radius: 4px;
			padding: 0 24px;
			box-shadow: inset 0px 4px 10px 0px rgba(147,147,147,0.302);
			outline: 4px solid #EAF5FF;
			margin: 0 20px;
			color: #2C2C2C;
			background: #96C6EE;
			width: auto;
			font-size: 14px;
			height: 40px;
		}
		.formModel_confirm:hover {
			background: rgba(150, 198, 238, 0.5);
			font-size: 16px;
		}
	}
</style>